'use client';

import { useState, useEffect } from 'react';
import {
    Plus,
    Search,
    Download,
    Trash2,
    Loader2,
    RefreshCcw,
    Edit,
    Filter,
    FileSpreadsheet
} from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import { supabase } from '@/lib/supabase';
import { exportToExcel } from '@/lib/export';

// Types matching the new Schema
type Student = {
    id: string;
    full_name: string;
    national_id: string;
    birth_date: string;
    gender: 'male' | 'female';
    religion: 'muslim' | 'christian';
    grade: string;
    class_group: string;
    phone: string;
    parent_job: string;
    address: string;
    status: 'active' | 'transferred' | 'expelled' | 'graduated';
    created_at: string;
};

export default function StudentsPage() {
    const [students, setStudents] = useState<Student[]>([]);
    const [loading, setLoading] = useState(true);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [searchTerm, setSearchTerm] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [editingId, setEditingId] = useState<string | null>(null);

    // Initial Form State
    const initialFormState = {
        full_name: '',
        national_id: '',
        birth_date: '',
        gender: 'male',
        religion: 'muslim',
        grade: 'الصف الأول الابتدائي',
        class_group: '',
        phone: '',
        parent_job: '',
        address: '',
        status: 'active'
    };

    const [formData, setFormData] = useState(initialFormState);

    const fetchStudents = async () => {
        setLoading(true);
        try {
            const { data, error } = await supabase
                .from('students')
                .select('*')
                .order('created_at', { ascending: false });

            if (error) throw error;
            setStudents(data as unknown as Student[] || []);
        } catch (error) {
            console.error('Error fetching students:', error);
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchStudents();
    }, []);

    // Automatic Data Extraction from National ID
    // 14 digits: C YY MM DD GOV SER C
    const handleNationalIdChange = (value: string) => {
        const newData = { ...formData, national_id: value };

        if (/^\d{14}$/.test(value)) {
            const century = parseInt(value[0]);
            const year = parseInt(value.substring(1, 3));
            const month = value.substring(3, 5);
            const day = value.substring(5, 7);
            const governorate = value.substring(7, 9); // e.g. 01 for Cairo
            const genderDigit = parseInt(value.substring(12, 13));

            const fullYear = (century === 2 ? 1900 : 2000) + year;
            newData.birth_date = `${fullYear}-${month}-${day}`;

            // Gender: Odd = Male, Even = Female
            newData.gender = genderDigit % 2 !== 0 ? 'male' : 'female';
        }
        setFormData(newData);
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        // Basic Validations
        if (!/^\d{14}$/.test(formData.national_id)) {
            alert('الرقم القومي غير صحيح (14 رقم)');
            return;
        }

        setIsSubmitting(true);
        try {
            if (editingId) {
                // Update
                const { error } = await supabase
                    .from('students')
                    .update(formData)
                    .eq('id', editingId);
                if (error) throw error;
            } else {
                // Insert
                const { error } = await supabase
                    .from('students')
                    .insert([formData]);
                if (error) throw error;
            }

            // Log action
            await supabase.from('logs').insert([{
                action: editingId ? 'تعديل طالب' : 'إضافة طالب',
                details: `الطالب: ${formData.full_name}`,
                user_id: 'admin'
            }]);

            setIsModalOpen(false);
            setEditingId(null);
            setFormData(initialFormState as any);
            fetchStudents();

        } catch (error: any) {
            alert('حدث خطأ: ' + error.message);
        } finally {
            setIsSubmitting(false);
        }
    };

    const handleDelete = async (id: string) => {
        if (!confirm('هل أنت متأكد من الحذف النهائي؟')) return;

        const { error } = await supabase.from('students').delete().eq('id', id);
        if (!error) {
            setStudents(prev => prev.filter(s => s.id !== id));
        }
    };

    const filteredStudents = students.filter(student =>
        student.full_name.includes(searchTerm) ||
        student.national_id.includes(searchTerm)
    );

    const statusMap: Record<string, { label: string, clazz: string }> = {
        'active': { label: 'منتظم', clazz: 'status-success' },
        'transferred': { label: 'محول', clazz: 'status-warning' },
        'expelled': { label: 'مفصول', clazz: 'status-error' },
        'graduated': { label: 'خريج', clazz: 'status-info' }
    };

    return (
        <div style={{ maxWidth: '1400px', margin: '0 auto' }}>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '2rem' }}>
                <div>
                    <h1 style={{ fontSize: '1.5rem', fontWeight: 'bold' }}>سجلات الطلاب</h1>
                    <p className="text-muted">إدارة بيانات الطلاب والملفات الأساسية</p>
                </div>
                <button
                    onClick={() => { setEditingId(null); setFormData(initialFormState as any); setIsModalOpen(true); }}
                    className="btn btn-primary"
                >
                    <Plus size={18} />
                    <span>تسجيل طالب جديد</span>
                </button>
            </div>

            {/* Filter Bar */}
            <div className="card" style={{ marginBottom: '1.5rem', padding: '1rem' }}>
                <div style={{ display: 'flex', gap: '1rem', alignItems: 'center' }}>
                    <div style={{ position: 'relative', flex: 1, maxWidth: '400px' }}>
                        <Search size={18} style={{ position: 'absolute', right: '12px', top: '50%', transform: 'translateY(-50%)', color: 'var(--text-muted)' }} />
                        <input
                            type="text"
                            placeholder="بحث بالاسم، الرقم القومي، أو الكود..."
                            className="input-field"
                            style={{ paddingRight: '40px' }}
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                        />
                    </div>
                    <div style={{ height: '2rem', width: '1px', background: 'var(--border)' }} />
                    <button onClick={fetchStudents} className="btn btn-outline" title="تحديث">
                        <RefreshCcw size={18} />
                    </button>
                    <button onClick={() => exportToExcel(students, 'students_db')} className="btn btn-outline">
                        <FileSpreadsheet size={18} />
                        <span>تصدير Excel</span>
                    </button>
                </div>
            </div>

            {/* Students Table */}
            <div className="table-wrapper">
                <table>
                    <thead>
                        <tr>
                            <th>الاسم الكامل</th>
                            <th>الرقم القومي</th>
                            <th>الصف / الفصل</th>
                            <th>تاريخ الميلاد</th>
                            <th>النوع</th>
                            <th>ت. ولي الأمر</th>
                            <th>الحالة</th>
                            <th>الإجراءات</th>
                        </tr>
                    </thead>
                    <tbody>
                        {loading ? (
                            <tr>
                                <td colSpan={8} style={{ padding: '3rem', textAlign: 'center' }}>
                                    <div className="flex-center" style={{ flexDirection: 'column', gap: '1rem', color: 'var(--text-muted)' }}>
                                        <Loader2 className="animate-spin" size={32} />
                                        <span>جاري تحميل قاعدة البيانات...</span>
                                    </div>
                                </td>
                            </tr>
                        ) : filteredStudents.length === 0 ? (
                            <tr>
                                <td colSpan={8} style={{ padding: '2rem', textAlign: 'center', color: 'var(--text-muted)' }}>
                                    لا توجد نتائج مطابقة للبحث.
                                </td>
                            </tr>
                        ) : (
                            filteredStudents.map((student) => (
                                <tr key={student.id}>
                                    <td>
                                        <div style={{ fontWeight: '600' }}>{student.full_name}</div>
                                        {student.religion && <div className="text-xs text-muted" style={{ marginTop: '2px' }}>{student.religion === 'muslim' ? 'مسلم' : 'مسيحي'}</div>}
                                    </td>
                                    <td style={{ direction: 'ltr', textAlign: 'right', fontFamily: 'monospace' }}>
                                        {student.national_id}
                                    </td>
                                    <td>
                                        <div>{student.grade}</div>
                                        <div className="text-xs text-muted">فصل: {student.class_group || 'غير محدد'}</div>
                                    </td>
                                    <td>{student.birth_date}</td>
                                    <td>{student.gender === 'male' ? 'ذكر' : 'أنثى'}</td>
                                    <td>{student.phone}</td>
                                    <td>
                                        <span className={`status-badge ${statusMap[student.status]?.clazz || 'status-info'}`}>
                                            {statusMap[student.status]?.label || student.status}
                                        </span>
                                    </td>
                                    <td>
                                        <div style={{ display: 'flex', gap: '0.5rem' }}>
                                            <button
                                                onClick={() => { setEditingId(student.id); setFormData(student as any); setIsModalOpen(true); }}
                                                className="btn btn-outline"
                                                style={{ padding: '0.4rem', border: 'none', background: '#f1f5f9' }}
                                            >
                                                <Edit size={16} color="var(--primary)" />
                                            </button>
                                            <button
                                                onClick={() => handleDelete(student.id)}
                                                className="btn btn-outline"
                                                style={{ padding: '0.4rem', border: 'none', background: '#fee2e2' }}
                                            >
                                                <Trash2 size={16} color="var(--danger)" />
                                            </button>
                                        </div>
                                    </td>
                                </tr>
                            ))
                        )}
                    </tbody>
                </table>
            </div>

            {/* Modal */}
            <AnimatePresence>
                {isModalOpen && (
                    <div style={{ position: 'fixed', top: 0, left: 0, right: 0, bottom: 0, background: 'rgba(0,0,0,0.6)', zIndex: 100, display: 'flex', alignItems: 'center', justifyContent: 'center', padding: '1rem' }}>
                        <motion.div
                            initial={{ opacity: 0, scale: 0.95 }}
                            animate={{ opacity: 1, scale: 1 }}
                            exit={{ opacity: 0, scale: 0.95 }}
                            className="card"
                            style={{ width: '100%', maxWidth: '800px', maxHeight: '90vh', overflowY: 'auto' }}
                        >
                            <div style={{ borderBottom: '1px solid var(--border)', paddingBottom: '1rem', marginBottom: '1.5rem', display: 'flex', justifyContent: 'space-between' }}>
                                <h2 style={{ fontSize: '1.25rem', fontWeight: 'bold' }}>{editingId ? 'تعديل بيانات طالب' : 'تسجيل طالب جديد'}</h2>
                                <button onClick={() => setIsModalOpen(false)}><Plus style={{ transform: 'rotate(45deg)' }} /></button>
                            </div>

                            <form onSubmit={handleSubmit} style={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', gap: '1.5rem' }}>

                                <div style={{ gridColumn: 'span 2' }}>
                                    <h3 style={{ fontSize: '1rem', fontWeight: '600', marginBottom: '1rem', color: 'var(--brand)' }}>البيانات الشخصية</h3>
                                </div>

                                <div style={{ gridColumn: 'span 2' }}>
                                    <label className="label">الرقم القومي (14 رقم)</label>
                                    <input
                                        required
                                        maxLength={14}
                                        value={formData.national_id}
                                        onChange={(e) => handleNationalIdChange(e.target.value)}
                                        className="input-field"
                                        style={{ fontFamily: 'monospace', letterSpacing: '1px' }}
                                        placeholder="الرقم القومي من شهادة الميلاد"
                                    />
                                </div>

                                <div style={{ gridColumn: 'span 2' }}>
                                    <label className="label">الاسم رباعي (كما في الشهادة)</label>
                                    <input required value={formData.full_name} onChange={e => setFormData({ ...formData, full_name: e.target.value })} className="input-field" />
                                </div>

                                <div>
                                    <label className="label">تاريخ الميلاد</label>
                                    <input type="date" required value={formData.birth_date} onChange={e => setFormData({ ...formData, birth_date: e.target.value })} className="input-field" />
                                </div>

                                <div>
                                    <label className="label">النوع</label>
                                    <select value={formData.gender} onChange={e => setFormData({ ...formData, gender: e.target.value as any })} className="input-field">
                                        <option value="male">ذكر</option>
                                        <option value="female">أنثى</option>
                                    </select>
                                </div>

                                <div style={{ gridColumn: 'span 2' }}>
                                    <h3 style={{ fontSize: '1rem', fontWeight: '600', marginBottom: '1rem', marginTop: '1rem', color: 'var(--brand)' }}>البيانات الدراسية</h3>
                                </div>

                                <div>
                                    <label className="label">الديانة</label>
                                    <select value={formData.religion} onChange={e => setFormData({ ...formData, religion: e.target.value as any })} className="input-field">
                                        <option value="muslim">مسلم</option>
                                        <option value="christian">مسيحي</option>
                                    </select>
                                </div>

                                <div>
                                    <label className="label">حالة القيد</label>
                                    <select value={formData.status} onChange={e => setFormData({ ...formData, status: e.target.value as any })} className="input-field">
                                        <option value="active">منتظم</option>
                                        <option value="transferred">محول من الخارج</option>
                                        <option value="expelled">مفصول</option>
                                    </select>
                                </div>

                                <div>
                                    <label className="label">الصف الدراسي</label>
                                    <select value={formData.grade} onChange={e => setFormData({ ...formData, grade: e.target.value })} className="input-field">
                                        <option value="الصف الأول الابتدائي">الصف الأول الابتدائي</option>
                                        <option value="الصف الثاني الابتدائي">الصف الثاني الابتدائي</option>
                                        <option value="الصف الثالث الابتدائي">الصف الثالث الابتدائي</option>
                                        <option value="الصف الرابع الابتدائي">الصف الرابع الابتدائي</option>
                                        <option value="الصف الخامس الابتدائي">الصف الخامس الابتدائي</option>
                                        <option value="الصف السادس الابتدائي">الصف السادس الابتدائي</option>
                                        {/* Prep stage if needed */}
                                    </select>
                                </div>

                                <div>
                                    <label className="label">الفصل / المجموعة</label>
                                    <input value={formData.class_group} onChange={e => setFormData({ ...formData, class_group: e.target.value })} className="input-field" placeholder="مثال: 1/1" />
                                </div>

                                <div style={{ gridColumn: 'span 2' }}>
                                    <h3 style={{ fontSize: '1rem', fontWeight: '600', marginBottom: '1rem', marginTop: '1rem', color: 'var(--brand)' }}>بيانات التواصل</h3>
                                </div>

                                <div>
                                    <label className="label">وظيفة ولي الأمر</label>
                                    <input value={formData.parent_job} onChange={e => setFormData({ ...formData, parent_job: e.target.value })} className="input-field" />
                                </div>

                                <div>
                                    <label className="label">رقم الهاتف</label>
                                    <input value={formData.phone} onChange={e => setFormData({ ...formData, phone: e.target.value })} className="input-field" placeholder="01xxxxxxxxx" />
                                </div>

                                <div style={{ gridColumn: 'span 2' }}>
                                    <label className="label">العنوان بالتفصيل</label>
                                    <input value={formData.address} onChange={e => setFormData({ ...formData, address: e.target.value })} className="input-field" />
                                </div>

                                <div style={{ gridColumn: 'span 2', display: 'flex', gap: '1rem', marginTop: '1rem', paddingTop: '1rem', borderTop: '1px solid var(--border)' }}>
                                    <button disabled={isSubmitting} type="submit" className="btn btn-primary" style={{ flex: 1 }}>
                                        {isSubmitting ? 'جاري الحفظ...' : 'حفظ البيانات'}
                                    </button>
                                    <button type="button" onClick={() => setIsModalOpen(false)} className="btn btn-outline" style={{ flex: 1 }}>
                                        إلغاء
                                    </button>
                                </div>
                            </form>
                        </motion.div>
                    </div>
                )}
            </AnimatePresence>
        </div>
    );
}
